@import url(./vendor/swiper-bundle.css);
@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../fonts/../fonts/Montserrat-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  outline: var(--color-orange);
}

ul,
ol {
  padding: 0;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

ul {
  list-style: none;
}

img {
  width: 100%;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: inherit;
}

button {
  cursor: pointer;
  border: 1px solid transparent;
  background-color: transparent;
}

address {
  font-style: normal;
}

html {
  height: 100%;
  scroll-behavior: smooth;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: #fff;
  color: #100d0d;
  line-height: 1.3;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;
  overflow-x: hidden;
}

.error {
  border: 2px #cf1515 solid;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: #ffe4b7;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main {
  flex-grow: 1;
}

.container,
.container-banner {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}

.container-banner {
  max-width: 1260px;
}

.title {
  font-size: 42px;
  font-weight: 600;
}

.subtitle {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.2;
}

.btn {
  background-color: #100d0d;
  color: #fff;
  border-radius: 10px;
}

.btn:hover {
  background-color: #f9af31;
}

.btn:active {
  background-color: #eaeaea;
}

.input,
.textarea {
  padding: 15px;
}

.tabs,
.pagination,
.tags {
  font-weight: 600;
  line-height: 1.6;
  padding: 8px 10px;
  color: #b3b3b3;
  letter-spacing: 0.3px;
  border-radius: 5px;
}

.tabs:hover,
.pagination:hover,
.tags:hover {
  color: #f9af31;
  background-color: #fff8eb;
}

.tabs:active,
.pagination:active,
.tags:active {
  color: #fff;
  background-color: #f9af31;
}

.tags {
  color: #777;
}

.header {
  border-bottom: 1px solid var(--color-border);
  padding: 20px 0;
  position: fixed;
  top: 0;
  z-index: 20;
  background-color: #fff;
  width: 100%;
}

@media (max-width: 768px) {
  .header {
    padding: 15px 0 10px;
  }
}

@media (max-width: 768px) {
  .header .logo {
    margin: 0 auto;
  }
}

.header__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__list.nav-list {
  margin-right: 30px;
}

@media (max-width: 768px) {
  .header__list.nav-list {
    display: block;
    margin-right: 0;
  }
}

@media (max-width: 768px) {
  .header__list .nav-list__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

@media (max-width: 768px) {
  .header__list .nav-list__link {
    padding: 15px 30px;
    display: block;
  }
}

.nav-list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-right: 55px;
  margin-left: auto;
}

.nav-list__item:not(:last-child) {
  margin-right: 50px;
}

@media (max-width: 992px) {
  .nav-list__item:not(:last-child) {
    margin-right: 25px;
  }
}

.nav-list__menu {
  display: none;
}

.nav-list__item {
  font-weight: 600;
}

.nav-list__link {
  text-transform: capitalize;
  position: relative;
  transition: color 0.3s linear;
}

@media (max-width: 768px) {
  .nav-list__btn {
    margin: 0 auto;
  }
}

.team__title {
  margin-bottom: 15px;
}

.team__text {
  font-size: 16px;
  color: #777;
  letter-spacing: 0.3px;
  max-width: 485px;
  width: 100%;
  margin-bottom: 30px;
}

.team__list {
  display: flex;
  justify-content: space-between;
}

.team__item:not(:last-child) {
  margin-right: 25px;
}

.team__img {
  margin-bottom: 25px;
}

.team__name {
  margin-bottom: 10px;
}

.team__profession {
  color: #777;
  line-height: 1.8;
}
